<template>
  <div class="top-container shadow" :hidden="hiddenCss">
    <div class="padding-sm" style="display:flex;">
      <img src="../../../assets/article.png" style="height:26px;width:26px">
      <div class="article-title padding-left-sm">热门文章</div>
    </div>

    <div
      class="top-container-article border-radius3 margin-bottom-xs"
      v-for="(item,index) in topArticles"
      :key="index"
      @click="$router.push({path: `/article/detail/${item.id}`})"
      style="padding:3px 0 3px 39px;"
    >
      <div>1.</div>
      <div>{{item.title}}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    topArticles: Array
  },
  data() {
    return {
      hiddenCss: false
    };
  },
  created() {
    let that = this;
    let smScreen = () => {
      let clientWidth = document.documentElement.clientWidth;

      let ua = navigator.userAgent;
      that.hiddenCss = clientWidth < 1200; // && !!ua.match(/AppleWebKit.*Mobile.*/); //是否为移动终端;
    };

    smScreen();
    window.onresize = window.onload = smScreen;
  }
};
</script>

<style scoped>
.top-container {
  position: fixed;
  top:100px;
  right: 60px;
  margin-top: 10px;
  height: 380px;
  width: 280px;
  background: rgba(255, 255, 255, 0.8);
  cursor: pointer;
  /* border: 1px solid #3babc7; */
}
.top-container-article {
  /* flex-wrap: wrap; */
  display: flex;
  font-size: 0.9rem;
  font-weight: 550;
}
.top-container-article:hover {
  transition: background 0.4s;
  background: rgb(39,168,218);
  color: white;
}
</style>

