<template>
    <div>
        <b-container class="navigate-children-container">
            <b-row align-h="center">
                <b-col cols="12" class="container-articles border-radius6 padding-sm">
                    <article-card
                            v-for="(item,index) in cards"
                            :key="index"
                            v-bind="item"
                            @click.native="readArticle(item)"
                            class="container-item margin-tb-sm border-top-groove border-radius6"
                    ></article-card>

                    <div class="text-center justify-center align-center more-item">
                        <!--loading-->
                        <b-spinner
                                v-if="loading"
                                variant="info"
                                label="Spinning"
                                style="width: 3rem;
                                height: 3rem;">
                        </b-spinner>

                        <!-- More Article -->
                        <div
                                v-if="!loading && moreButton"
                                @click="loadArticleList()"
                                class="bg-gradual-blue shadow sm round text-center"
                                style="width: 120px;height: 32px;line-height: 32px;font-size: 0.9em;">加载更多
                        </div>
                    </div>
                </b-col>
            </b-row>
        </b-container>


        <!-- TopArticle -->
        <TopArticle
                class="border-radius3 border-top-groove"
                v-bind:topArticles="topArticles"
        ></TopArticle>
    </div>


</template>

<script>
    import ArticleCard from "@/components/components/article/ArticleCard.vue";
    import TopArticle from "@/components/components/article/TopArticle.vue";

    export default {
        name: 'ArticleListContainer',
        components: {
            ArticleCard,
            TopArticle
        },
        data() {
            return {
                loading: true,
                moreButton: false,
                pageIndex: 1,
                cards: [
                    // {
                    //     id: "123456",
                    //     title: "教UI系列教程 - 球形火花动效",
                    //     content:
                    //         "球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效球形火花动效", //正文
                    //     iconImg:
                    //         "https://img.zcool.cn/community/01ee935c7fa03ea801217ce601572d.jpg@520w_390h_1c_1e_1o_100sh.jpg", //图片展示
                    //     publishTime: "2019-02-27 21:40", //发表时间
                    //     authorIcon:
                    //         "https://img.zcool.cn/community/012d225c7378dfa801213f26384733.jpg@160w_160h_1c_1e_1o_100sh.jpg", //作者头像
                    //     author: "MicroGalaxy", //作者
                    //     watch: "636", //阅读量
                    //     comment: "126", //评论
                    //     collect: "360", //收藏
                    //     fromLable: "原创", //原创、转载
                    //     articleLable: ["UI", "HTML"] //文章标签：VUE JAVA SpringBoot ...
                    // }
                ],
                topArticles: [
                    {
                        id: "123",
                        title: "服务器网络故障排查"
                    },
                    {
                        id: "123",
                        title: "服务器网络故障排查"
                    }
                ]
            };
        },

        created() {
            this.loadArticleList()
            this.loadHotArticle()
        },

        methods: {
            //Article List
            loadArticleList() {
                this.loading = true
                this.$http({
                    url: this.$http.baseUrl(`/article/list`),
                    method: 'get',
                    params: this.$http.getParamsMerge({
                        'page': this.pageIndex
                    })
                }).then(({data}) => {
                    if (data && data.code === 0) {
                        this.cards = this.cards.concat(data.page.list)
                        this.$nextTick(() => {
                            this.loading = false
                            this.moreButton = data.page.totalPage > this.pageIndex
                            this.pageIndex++
                        })
                    } else {
                        return Promise.reject(data.msg)
                    }
                }).catch(msg => {
                    this.$bvToast.toast(msg.toString(), {
                        title: '错误',
                        toaster: 'b-toaster-top-center',
                        autoHideDelay: 5000
                    })
                })
            },

            //Hot Article
            loadHotArticle() {
                this.$http({
                    url: this.$http.baseUrl(`/article/list/hot`),
                    method: 'get'
                }).then(({data}) => {
                    if (data && data.code === 0) {
                        this.topArticles = data.hotList
                    } else {
                        return Promise.reject(data.msg)
                    }
                }).catch(msg => {
                    this.$bvToast.toast(`hot articles load fail：${msg.toString()}`, {
                        title: '错误',
                        toaster: 'b-toaster-top-center',
                        autoHideDelay: 5000
                    })
                })
            },

            //Article Detail
            readArticle(article) {
                this.$router.push({path: `/article/detail/${article.id}`})
            }
        }
    };
</script>

<style scoped>
    .container-articles {
        /* border-radius: 6px; */
        background: rgba(255, 255, 255, 0);
        /* box-shadow:0px 40px 100px 0px rgba(0, 0, 0, 0.03); */
        /* opacity: 0.85; */
    }

    .container-item {
        float: left;
        max-width: 720px;
        width: 100%;
        box-shadow: 0px 6px 18px rgba(0, 0, 0, 0.07);
        transition: all 0.3s;
    }

    .container-item:hover {
        transform: scale(1.05, 1.05);
        box-shadow: 0px 18px 38px rgba(80, 80, 80, 0.2) !important;
        transition: all 0.3s;
    }

    .more-item {
        float: left;
        max-width: 720px;
        width: 100%;
        display: flex;
        height: 60px;
        cursor: pointer;
    }

    /* 热门文章 */
    .topArticle {

    }
</style>